import Vue from 'vue'
import { messageDuration } from '@/config'
import { Message, Notice } from 'view-design'

/**
 * @description Message
 * @param {string|VNode} message 
 * @param {'success'|'warning'|'info'|'error'} type 
 * @param {string} customClass 
 * @param {boolean} dangerouslyUseHTMLString messageHTML
 */
Vue.prototype.$baseMessage = (
  message,
  type = 'info',
  customClass = undefined,
  dangerouslyUseHTMLString = false
) => {
  Message({
    message,
    type,
    customClass,
    duration: messageDuration,
    dangerouslyUseHTMLString,
    showClose: true,
  })
}

/**
 * @description Notification
 * @param {string} message 
 * @param {string|VNode} title 
 * @param {'success'|'warning'|'info'|'error'} type ,
 * @param {'top-right'|'top-left'|'bottom-right'|'bottom-left'} position 
 * @param duration ,
 */
Vue.prototype.$baseNotify = (
  desc,
  title,
  type = 'success',
  position = 'top-right',
  duration = messageDuration
) => {
  Notice({
    title,
    desc,
    type,
    duration,
    position,
  })
}

/**
 * @description 
 * @param {*} formType
 */
Vue.prototype.$baseTableHeight = (formType) => {
  let height = window.innerHeight
  const paddingHeight = 291
  const formHeight = 60

  if ('number' === typeof formType) {
    height = height - paddingHeight - formHeight * formType
  } else {
    height = height - paddingHeight
  }
  return height
}
