/**
 * @description token
 */
import Vue from 'vue'
import { getToken, removeToken, setToken } from '@/utils/token'
import { getCompanyId, setCompanyId } from '@/utils/companyId'
import { getPhone, setPhone } from '@/utils/phone'
import { getShopId, setShopId } from '@/utils/shopId'
import { setUid, getUid } from '@/utils/uid'
import Cookies from 'js-cookie'
import { logout } from '@/api/user'

const getters = {
  token: (state) => state.token,
  companyId: (state) => state.companyId,
  shopId: (state) => state.shopId,
  uid: (state) => state.uid,
  username: (state) => state.username,
  phone: (state) => state.phone,
  avatar: (state) => state.avatar,
}
const state = () => ({
  token: getToken(),
  companyId: getCompanyId(),
  shopId: getShopId(),
  uid: getUid(),
  username: '',
  phone: getPhone(),
  isOnLine: 0,
  avatar: '',
  unreadCount: 0,
})
const mutations = {
  /**
   * @description token
   * @param {*} state
   * @param {*} token
   */
  setToken(state, token) {
    state.token = token
    setToken(token)
  },
  /**
   * @description companyId
   * @param {*} state
   * @param {*} companyId
   */
  setCompanyId(state, companyId) {
    state.companyId = companyId
    setCompanyId(companyId)
  },
  /**
   * @description phone
   * @param {*} state
   * @param {*} phone
   */
  setPhone(state, phone) {
    state.phone = phone
    setPhone(phone)
  },
  /**
   * @description userModel
   * @param {*} state
   * @param {*} userModel
   */
  setUserModel(state, userModel) {
    state.userModel = userModel
    setUserModel(userModel)
  },
  /**
   * @description shopId
   * @param {*} state
   * @param {*} companyId
   */
  setShopId(state, shopId) {
    state.shopId = shopId
    setShopId(shopId)
  },
  /**
   * @description uid
   * @param {*} state
   * @param {*} username
   */
  setUid(state, uid) {
    state.uid = uid
    setUid(uid)
  },
  /**
   * @description 
   * @param {*} state
   * @param {*} username
   */
  setUsername(state, username) {
    state.username = username
  },
  /**
   * @description 
   * @param {*} state
   * @param {*} isOnLine
   */
  setIsOnLine(state, isOnLine) {
    state.isOnLine = isOnLine
  },
  /**
   * @description 
   * @param {*} state
   * @param {*} avatar
   */
  setAvatar(state, avatar) {
    state.avatar = avatar
  },
  /**
   * @description Token
   * @param {*} state
   * @param {*} conversationToken
   */
  setConversationToken(state, conversationToken) {
    state.conversationToken = conversationToken
    setConversationToken(conversationToken)
  },
}
const actions = {
  /**
   * @description 
   * @param {*} { commit, dispatch }
   */
  setVirtualRoles({ commit, dispatch }) {
    dispatch('acl/setFull', true, { root: true })
    commit('setAvatar', 'https://i.gtimg.cn/club/item/face/img/2/15922_100.gif')
    commit('setUsername', 'admin()')
  },
  /**
   * @description 
   * @param {*} { commit }
   * @param {*} userInfo
   */
  async login({ commit }, userInfo) {
    const {
      data: { [tokenName]: token },
    } = await login(userInfo)
    if (token) {
      commit('setToken', token)
      const hour = new Date().getHours()
      const thisTime =
        hour < 8
          ? ''
          : hour <= 11
          ? ''
          : hour <= 13
          ? ''
          : hour < 18
          ? ''
          : ''
      Vue.prototype.$baseNotify(`${title}`, `${thisTime}`)
    } else {
      const err = `${tokenName}...`
      Vue.prototype.$baseMessage(err, 'error', 'vab-hey-message-error')
      throw err
    }
  },
  /**
   * @description 
   * @param {*} {}
   * @param {*} tokenData
   */
  async socialLogin({ commit }, tokenData) {
    const {
      data: { [tokenName]: token },
    } = await socialLogin(tokenData)
    if (token) {
      commit('setToken', token)
      const hour = new Date().getHours()
      const thisTime =
        hour < 8
          ? ''
          : hour <= 11
          ? ''
          : hour <= 13
          ? ''
          : hour < 18
          ? ''
          : ''
      Vue.prototype.$baseNotify(`${title}`, `${thisTime}`)
    } else {
      const err = `loginJSON${tokenName}...`
      Vue.prototype.$baseMessage(err, 'error', 'vab-hey-message-error')
      throw err
    }
  },
  handleLogOut({ state, commit }) {
    return new Promise((resolve, reject) => {
      logout(state.token)
        .then(() => {
          commit('setToken', '')
          commit('setAccess', [])
          resolve()
        })
        .catch((err) => {
          reject(err)
        })
      // logout
      // commit('setToken', '')
      // commit('setAccess', [])
      // resolve()
    })
  },
  /**
   * @description  
   * @param {*} { commit, dispatch, state }
   * @returns
   */
  async getUserInfo({ commit, dispatch }) {
    const {
      data: {
        uid,
        name,
        phone,
        portrait,
        roles,
        permissions,
        isOnLine,
        docVersion,
        docTitle,
        docBody,
        docPath,
        authUserList,
      },
    } = await getUserInfo()
    /**
     * ,,RolesPermissions
     * username {String}
     * avatar {String}
     * roles {List}
     * ability {List}
     */
    if (
      (uid && !isString(uid)) ||
      (name && !isString(name)) ||
      (portrait && !isString(portrait)) ||
      (roles && !isArray(roles)) ||
      (permissions && !isArray(permissions))
    ) {
      const err = 'getUserInfoJSON'
      Vue.prototype.$baseMessage(err, 'error', 'vab-hey-message-error')
      throw err
    } else {
      if (uid) commit('setUid', uid)
      if (name) commit('setUsername', name)
      if (phone) commit('setPhone', phone)
      if (portrait) commit('setAvatar', portrait)
      if (roles) dispatch('acl/setRole', roles, { root: true })
      // permissions,
      if (permissions)
        dispatch('acl/setPermission', permissions, { root: true })
      if (isOnLine !== null && isOnLine !== undefined) {
        commit('setIsOnLine', isOnLine)
      }

      if (authUserList) {
        let userModelArr = []
        for (let item of authUserList) {
          userModelArr.push(item.appModelId)
        }
        commit('setUserModel', userModelArr)
      }
    }

    //
    if (localStorage.getItem('docVersion') != docVersion) {
      Vue.prototype.$baseNotify(
        docBody,
        docTitle,
        'success',
        'bottom-right',
        30000,
        docPath
      )
    }

    localStorage.setItem('docVersion', docVersion)
  },
  /**
   * @description 
   * @param {*} { dispatch }
   */
  async logout({ dispatch }) {
    await logout()
    await dispatch('resetAll')
    toLoginRoute()
  },
  /**
   * @description tokenrolespermissionroutertabsBar
   * @param {*} { commit, dispatch }
   */
  async resetAll({ commit, dispatch }) {
    let rootDomain = '27yn.cn'
    if (process.env.VUE_APP_ENV == 'production') {
      rootDomain = 'lyfz.net'
    }

    let keys = document.cookie.match(/[^ =;]+(?==)/g)
    if (keys) {
      for (let i = keys.length; i--; ) {
        document.cookie =
          keys[i] + '=0;path=/;expires=' + new Date(0).toUTCString()
        if (keys[i] == 'ticket' || keys[i] == 'version') {
          document.cookie =
            keys[i] +
            `=0;path=/;domain=${rootDomain};expires=` +
            new Date(0).toUTCString()
        }
      }
    }

    commit('setUsername', '')
    commit('setAvatar', 'https://i.gtimg.cn/club/item/face/img/2/15922_100.gif')
    commit('routes/setRoutes', [], { root: true })
    await dispatch('setToken', '')
    await dispatch('setUid', '')
    await dispatch('setCompanyId', '')
    await dispatch('setUserModel', {})
    await dispatch('setShopId', '')
    await dispatch('setPhone', '')
    await dispatch('acl/setFull', false, { root: true })
    await dispatch('acl/setRole', [], { root: true })
    await dispatch('acl/setPermission', [], { root: true })
    await dispatch('tabs/delAllVisitedRoutes', null, { root: true })
    await resetRouter()
    removeToken()
  },
  /**
   * @description token
   * @param {*} { commit }
   * @param {*} token
   */
  setToken({ commit }, token) {
    commit('setToken', token)
  },
  /**
   * @description companyId
   * @param {*} { commit }
   * @param {*} companyId
   */
  setCompanyId({ commit, dispatch }, companyId) {
    Cookies.set('ccId', companyId, { path: '/' })
    Cookies.set('ccId', companyId, { path: '/', domain: '27yn.cn' })
    Cookies.set('ccId', companyId, { path: '/', domain: 'lyfz.net' })

    commit('setCompanyId', companyId)
    dispatch('getUserInfo')
  },
  /**
   * @description uid
   * @param {*} { commit }
   * @param {*} uid
   */
  setUid({ commit }, uid) {
    commit('setUid', uid)
  },
  /**
   * @description shopId
   * @param {*} { commit }
   * @param {*} shopId
   */
  setShopId({ commit }, shopId) {
    commit('setShopId', shopId)
  },
  /**
   * @description 
   * @param {*} { commit }
   * @param {*} avatar
   */
  setAvatar({ commit }, avatar) {
    commit('setAvatar', avatar)
  },
  /**
   * @description 
   * @param {*} { commit }
   * @param {*} userName
   */
  setUsername({ commit }, userName) {
    commit('setUsername', userName)
  },
  /**
   * @description 
   * @param {*} { commit }
   * @param {*} isOnLine
   */
  setIsOnLine({ commit }, isOnLine) {
    commit('setIsOnLine', isOnLine)
  },
  /**
   * @description Token
   * @param {*} { commit }
   * @param {*} conversationToken
   */
  setConversationToken({ commit }, conversationToken) {
    commit('setConversationToken', conversationToken)
  },
}
export default { state, getters, mutations, actions }
