import Vue from 'vue'
import axios from 'axios'
import { cacheAdapterEnhancer } from 'axios-extensions'
import {
  baseURL,
  contentType,
  messageName,
  requestTimeout,
  statusName,
  successCode,
} from '@/config'
import store from '@/store'
import qs from 'qs'
import { isArray } from '@/utils/validate'

let loadingInstance

// Code
const codeVerificationArray = isArray(successCode)
  ? [...successCode]
  : [...[successCode]]

const CODE_MESSAGE = {
  0: 'CORS',
  200: '',
  201: '',
  202: '()',
  204: '',
  400: '',
  401: '()',
  402: '',
  403: '',
  404: '',
  406: '',
  410: '',
  500: '',
  502: '',
  503: '',
  504: '',
  6001: '',
}

/**
 * axios
 * @param config
 * @returns {any}
 */
const requestConf = (config) => {
  const token = store.getters['user/token']
  console.log('token', token)
  // const companyId = store.getters['user/companyId']
  // const shopId = store.getters['user/shopId']

  //  setting.config.js tokenNameheaders
  // if (token) config.headers[tokenName] = token

  // config.headers['companyId'] = companyId
  // config.headers['shopId'] = shopId

  //  
  // if (token) config.headers['ticket'] =

  config.headers['token'] = token

  if (
    config.data &&
    config.headers['Content-Type'] ===
      'application/x-www-form-urlencoded;charset=UTF-8'
  )
    config.data = qs.stringify(config.data)
  return config
}

/**
 * axios
 * @param data response
 * @param status HTTP status
 * @param statusText HTTP status text
 * @returns {Promise<*|*>}
 */
const handleData = async ({ data, status = 0, statusText }) => {
  if (loadingInstance) loadingInstance.close()
  // data.codecode
  let code = data && data[statusName] ? data[statusName] : status
  // codecodecode200
  if (codeVerificationArray.indexOf(code) + 1) code = 200
  switch (code) {
    case 200:
      // restful()
      // 
      // { code: 1, msg: '' }
      // { code: 200, data: {  }, msg: '' }
      // return data
      return data
    case 401:
      break
    case 403:
      break
    case 6001:
      return
  }
  // 
  // data.msg
  const errMsg = `${
    data && data[messageName]
      ? data[messageName]
      : CODE_MESSAGE[code]
      ? CODE_MESSAGE[code]
      : statusText
  }`
  Vue.prototype.$baseMessage(errMsg, 'error', 'vab-hey-message-error')
  // (errorHandler)
  return Promise.reject(data)
}

/**
 * @description axios
 */
const instance = axios.create({
  baseURL,
  timeout: requestTimeout,
  headers: {
    'Content-Type': contentType,
  },
  adapter: cacheAdapterEnhancer(axios.defaults.adapter, {
    enabledByDefault: false,
    cacheFlag: 'useCache',
  }),
})

/**
 * @description axios
 */
instance.interceptors.request.use(requestConf, (error) => {
  return Promise.reject(error)
})

/**
 * @description axios
 */
instance.interceptors.response.use(
  (response) => handleData(response),
  (error) => {
    const { response = {} } = error
    return handleData(response)
  }
)

export default instance
